input:-webkit-autofill,
input:-webkit-autofill:active,
input:-webkit-autofill:focus,
input:-webkit-autofill:hover {
	-webkit-box-shadow: 0 0 0 1000px white inset !important;
}

.download-loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;

    & > div {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%,-50%,0);
        width: 100%;
        height: 100%;
        max-height: 50px;
        max-width: 50px;
    }

    .icon-loading {
        width: 100%;
    }
}

.placeholder {
    background-color: transparent;
    width: 100%;

    svg {
        height: inherit;
        min-height: inherit;
    }
}